<template>
  <div class="home">


    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="item in list" :key="item" :title="item" />
    </van-list>


  </div>
</template>
<script>
  import {
    Swipe,
    SwipeItem,
    Search,
    List,
    
    Cell
  } from 'vant';

  export default {
    data() {
      return {
        list: [],
        loading: false,
        finished: false

      }
    },
    components: {
     
      [List.name]: List,
      [Cell.name]:Cell
    },
  methods:{
        onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        // if (this.list.length >= 100) {
        //   this.finished = true;
        // }
      },2000);
    }
  }
  

  }

</script>
<style>


</style>



